.laderArea {
  text-align: center;
}

.loader {
  position: relative;
  border: .5rem solid #f3f3f3;
  border-radius: 50%;
  border-top: .5rem solid #3498db;
  width: 4rem;
  height: 4rem;
  animation: spin 1s linear infinite;
  margin: 0 auto;
  .loaderText {
    position: absolute;
  }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}