$pc-media:960px;
$pcs-media:959px;
$pad-media:760px;
$pads-media:759px;
$phone-media:480px;
$phones-media:479px;

@mixin pc-width() {
  @media all and (min-width: $pc-media) {
    @content;
  }
}
@mixin pad-width() {
  @media all and (min-width:$pad-media) and (max-width: $pcs-media) {
    @content;
  }
}
@mixin small-pad-width() {
  @media all and (min-width: $phone-media) and (max-width:$pads-media) {
    @content;
  }
}
@mixin phone-width() {
  @media all and (max-width: $phones-media) {
    @content;
  }
}

.mainContent {
  max-width: $pad-media;
  margin: 1rem auto;

  @include pc-width {
    width: 50%;
  }
  @include pad-width {
    width: 80%;
  }
  @include small-pad-width {
    width: 90%;
  }
  @include phone-width {
    width: 95%;
  }
}
